<template>
  <v-container fluid>
    <!--一个小横幅，说明这里是用作图书销售的-->
    <v-row>
      <v-col cols="12">
        <v-banner two-line>
          <!--小图标-->
          <v-avatar slot="icon" color="deep-purple accent-4" size="40">
            <v-icon icon="mdi-lock" color="white">mdi-book</v-icon>
          </v-avatar>
          <!--文字介绍-->
          Let's see what books you want to buy
        </v-banner>
      </v-col>
    </v-row>
    <!--新品上架-->
    <v-row>
      <v-col cols="12">
        <!--新品上架的文字-->
        <v-row justify="center">
          <div class="display-2 orange--text text-center">New Books For Sale!</div>
        </v-row>
        <!--小字，图书推荐-->
        <v-row justify="center">
          <div class="grey--text">There are books we recommand!</div>
        </v-row>
        <!--图书，只展示四本-->
        <v-row>
          <v-col sm="2" v-for="(item, i) in bookInventories" :key="i">
            <v-sheet @click="openDetail(item.id)">
              <v-img v-if="item.icon" :src="item.icon" alt="销售图书" width="180" height="220" class="mx-auto"></v-img>
              <v-img v-else src="../../assets/sale/book.jpeg" alt="销售图书" width="180" height="220" class="mx-auto"></v-img>
              <div class="text-center mt-2"><span>{{ item.price }}</span><span class="ml-1">RMB</span></div>
            </v-sheet>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
    <!--卖家评论-->
    <v-row>
      <v-col cols="12">
        <v-timeline :dense="$vuetify.breakpoint.smAndDown">
          <v-timeline-item
            v-for="(item, i) in users"
            :key="i"
            large
          >
            <template v-slot:icon>
              <v-avatar>
                <img src="../../assets/logo.png">
              </v-avatar>
            </template>
            <template v-slot:opposite>
              <span>{{ item.username }}</span>
            </template>
            <v-card class="elevation-2">
              <v-card-title class="text-h5">
                {{ item.title }}
              </v-card-title>
              <v-card-text>{{ item.comment }}</v-card-text>
            </v-card>
          </v-timeline-item>
        </v-timeline>
      </v-col>
    </v-row>
    <!--分类-->
    <v-row>
      <v-col>
        <v-row>
          <div class="display-2 ml-2">Learn More?</div>
        </v-row>
        <v-row>
          <v-col sm="2" v-for="(item, i) in categories" :key="i">
            <v-btn outlined color="primary" width="100" link :to="{ name: 'BookSaleList', params: { categoryId: item.id }}">{{ item.name }}</v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: 'BookSaleHome',
  data: () => ({
    // 各种分类
    categories: [],
    // 图书
    bookInventories: [],
    // 用户评论
    users: [
      {
        username: 'tomcat',
        title: 'This is a good book!',
        comment: 'The content of Journey to the West is very interesting. It tells the story of the journey of Tang Priest and his disciples to fetch scriptures from the West. They went through 91 difficulties and finally returned to the West.'
      },
      {
        username: 'spring',
        title: 'The book is so interesting!',
        comment: 'The novel mainly describes a named Douro mainland of the world of the soul. Tang door disciple Tang three, because of stealing the inner door for the Tang door is not allowed, but found through to the other world, that is, the Douro mainland soul village. There is no magic, no martial arts, no fighting ability, but there is a magical spirit.'
      }
    ]
  }),
  methods: {
    initialize () {
      // 初始化页面，调用多个方法
      this.getCategories()
      this.getLatestBookInventories()
    },
    getCategories () {
      // 获取所有分类
      this.axios.get(
        '/api/bookCategories'
      ).then(response => {
        this.categories = response.data._embedded.bookCategories
      })
    },
    getLatestBookInventories () {
      // 获取最新录入的图书
      this.axios.get(
        '/api/bookInventories',
        {
          params: {
            size: 6
          }
        }
      ).then(response => {
        this.bookInventories = response.data._embedded.bookInventories
        this.page = response.data.page
        this.page.number += 1
      })
    },
    openDetail (id) {
      this.$router.push({
        name: 'BookSaleDetail',
        params: {
          inventoryId: id
        }
      })
    }
  },
  created () {
    // 页面加载时，调用初始化
    this.initialize()
  }
}
</script>
